@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://maxcdn.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.wrapper{background:black;padding:30px 100px;min-height:100vh}@media screen and (max-width: 850px){.wrapper{padding:30px}}.header-row{padding:15px;margin:15px;text-align:center;background-color:#d3d3d3;margin-bottom:10px}@media screen and (max-width: 850px){.header-row{background-color:white}}.row-panel{text-align:center}.row-panel .column{text-align:center;padding:2.5% 0;margin:0 1.6666%;width:30%;text-transform:wrap}.row-panel .row:after{content:"";display:table;clear:both}@media screen and (max-width: 600px){.row-panel .column{padding:0px 5%;margin:0 5%;margin-top:5%;width:100%;margin-bottom:20px}}.data-panel{margin:0px 30px;text-align:center;color:#d8d8d8}.data-panel .column{float:left;width:50%;padding:10px;border:10px}.data-panel .column-4{text-align:center;padding:2.5%;margin:1.6666%;margin-bottom:0px;width:30%;border:1px solid #b4b4b4;text-transform:wrap}.data-panel .chart{margin:0px 20%}.data-panel .row:after{content:"";display:table;clear:both}.data-panel .header{font-weight:600;font-size:28px;text-align:center}.data-panel .title{padding-bottom:0px}@media screen and (max-width: 600px){.data-panel .column{width:100%;margin-bottom:20px}.data-panel .chart{margin:0px}}@media screen and (max-width: 850px){.data-panel .title span{font-size:14px;padding:0px 20px}.data-panel .column-4{width:100%;margin-bottom:20px}.data-panel .chart{margin:0px}}.chart-modal{color:white;background-color:black;text-align:center;border:1px solid #b4b4b4}.chart-modal .close{color:#d8d8d8;background-color:black}.chart-modal .close:hover{color:#ffffff}.btn{background-color:#d8d8d8;border:0px;color:black;border-radius:10px;transition:0.4s;border:0;cursor:pointer}.btn:hover{background-color:#00ccff81;color:#ffffff;border-radius:30px}#goToTopButton{display:none;position:fixed;bottom:40px;right:30px;z-index:99;background-color:#00ccff81;border:0px;background-color:#d8d8d8;border-radius:10px;transition:0.4s;border:0;cursor:pointer}@media screen and (max-width: 850px){#goToTopButton{font-size:12px;right:5px}}#goToTopButton:hover{background-color:#00ccff81;color:#ffffff;border-radius:30px}#goToGlobalButton{display:block;position:fixed;bottom:40px;right:30px;z-index:99;background-color:#00ccff81;border:0px;background-color:#d8d8d8;border-radius:10px;transition:0.4s;border:0;cursor:pointer}@media screen and (max-width: 850px){#goToGlobalButton{font-size:12px;right:5px}}#goToGlobalButton:hover{background-color:#00ccff81;color:#ffffff;border-radius:30px}.globalView{margin-bottom:5%;background-color:black;color:greenyellow}@media screen and (max-width: 850px){.globalView{display:none}}.fixed-row{padding:20px;min-height:350px}.padding-top-xlg{padding-top:50px}@media screen and (max-width: 850px){.padding-top-xlg{padding-top:20px}}.padding-top-lg{padding-top:30px}@media screen and (max-width: 850px){.padding-top-lg{padding-top:20px}}.wrapper-box{padding:15px}.modalRow{text-align:left;padding:5% 10%}.addon{position:absolute;right:3px;bottom:6px}.addon span{background:white;border:0;height:28px;font-size:14px}.addon button{position:absolute;color:lightgray;background:transparent;font-size:14px;padding:0px 10px 0px 0px;margin-top:0.5px;width:auto;border-radius:0px 3px 3px 0px !important;margin-right:-1px;border:0;text-align:center;box-shadow:none}.addon button:not(:disabled):not(.disabled):active,.addon button:hover,.addon button:active,.addon button:focus{background:white;color:gray;box-shadow:none}.addon button i{margin-left:5px;vertical-align:middle}.modal-lg{max-width:50%}.modal-lg .modal-body{padding:10px 40px}@media screen and (max-width: 600px){.modal-lg{max-width:100%;padding-top:25%}.modal-lg .modal-body{padding:10px}}

